.modal-wrap {
  display: block;
  position: fixed;
  z-index: 9999;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.44);
}
#modal-custom {
  display: block;
  top: 50%;
  margin: 0 auto;
  width: 350px;
  height: 151px;
  position: relative;
  background: white;
  border: 1px solid rgba(58, 58, 58, 0.29);
  border-radius: 6px;
}



